* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  body {
    background: linear-gradient(
      90deg,
      rgb(204, 0, 250) 0%,
      rgb(0, 217, 255) 100%
    );
  }
  
  .todo-app {
    display: flex;
    flex-direction: column;
    justify-content: center; 
    align-items: center; 
    width: 520px;
    min-height: 600px;
    background: #161a2b;
    text-align: center;
    margin: 30px auto; 
    border-radius: 10px;
    padding-bottom: 20px;
   
  }
  
  h1 {
    margin: 50px 0;
    color: #fff;
    font-size: 24px;
    margin-top: -50px;
  }
  
  .complete {
    text-decoration: line-through;
    opacity: 0.4;
  }
  
  .todo-form {
    margin-bottom: 32px;
  }
  
  .todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
  }
  
  .todo-input::placeholder {
    color: #e2e2e2;
  }
  
  .todo-button {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
      90deg,
      rgba(93, 12, 255, 1) 0%,
      rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    text-transform: capitalize;
  }
  
  .todo-input.edit {
    border: 2px solid #149fff;
  }
  
  .todo-button.edit {
    background: linear-gradient(
      90deg,
      rgba(20, 159, 255, 1) 0%,
      rgba(17, 122, 255, 1) 100%
    );
    padding: 16px 22px;
  }
  
  .search-container {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }
  
  .search-container input {
    padding: 10px;
    border-radius: 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 320px;
    background: transparent;
    color: #fff;
    margin-right: 8px;
  }
  
  .search-container .search-icon {
    font-size: 20px;
    color: #fff;
    cursor: pointer;
  }
  
  .no-results {
    color: #fff;
    font-size: 18px;
    margin-top: 20px;
  }
  
  .todo-container {
    display: flex;
    flex-direction: row;
    position: relative;
  }
  
  .todo-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 4px auto;
    color: #fff;
    background: linear-gradient(
      90deg,
      rgba(255, 118, 20, 1) 0%,
      rgba(255, 84, 17, 1) 100%
    );
    padding: 16px;
    border-radius: 5px;
    width: 90%;
  }
  
  .todo-row:nth-child(4n + 1) {
    background: linear-gradient(
      90deg,
      rgba(93, 12, 255, 1) 0%,
      rgba(155, 0, 250, 1) 100%
    );
  }
  
  .todo-row:nth-child(4n + 2) {
    background: linear-gradient(
      90deg,
      rgba(255, 12, 241, 1) 0%,
      rgba(250, 0, 135, 1) 100%
    );
  }
  
  .todo-row:nth-child(4n + 3) {
    background: linear-gradient(
      90deg,
      rgba(20, 159, 255, 1) 0%,
      rgba(17, 122, 255, 1) 100%
    );
  }
  
  .icons {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
  }
  
  .delete-icon {
    margin-right: 5px;
    color: #fff;
  }
  
  .edit-icon {
    color: #fff;
  }
  
  .todo-row.complete {
    text-decoration: line-through;
    opacity: 0.6;
  }
  
  .todo-header {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    align-items: center;
  }
  
  .expand-icon {
    font-size: 20px;
  }
  
  .todo-details {
    padding: 10px;
    border-top: 1px solid #ddd;
    background: #f9f9f9;
  }
  
  .todo-description-input {
    width: 100%;
    height: 100px;
    padding: 8px;
    border-radius: 4px;
    border: 1px solid #ddd;
    box-sizing: border-box;
  }
  
  .timestamp {
    font-size: 12px;
    color: #888;
    margin-top: 8px;
  }
  
  .edit-icon {
    margin-left: 10px;
    cursor: pointer;
  }
  